.navigation {
   background-color: #000000;
   
   .container {
      justify-content: space-between;
      align-items:center;
   }

   img {
      padding: 10px 0;
   }

   &__list {
      color: white;
      list-style-type: none;
      display: flex;
      justify-content: space-between;
      text-transform: uppercase;

      li {
         padding-right: 26px;
         font-size: 18px;
         line-height: 25px;
      }

      li:last-child {
         padding: 0;
      }
   }
}